
import { defineComponent } from 'vue'
import { ICM208002SC01_REQ_Body } from '@/shared/type/ICM208002SC01.type';
import { modalController } from '@ionic/vue';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { screenMode } from '@/shared/type/Approval.type';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';

export default defineComponent({
    name: 'icm208002sc01',

    components: {
        ModifyCard,
        BreadCrumb
    },

    data() {
        return {
            modifyCard: {
                registerTellerId: '',
                registerTellerName: '',
                registerDate: '',
                registerTime: '',
                updatedTellerId: '',
                updatedTellerName: '',
                updatedDate: '',
                updatedTime: '',
                deleteTellerId: '',
                deleteTellerName: '',
                deleteDate: '',
                deleteTime: '',
            },
            termAndConditionCategoryDetail: {
                categoryCode: "005",
                categoryName: "",
                categoryDesc: "",
                statusCode: ""
            },
            isEdit: true as boolean,
            isDisabled: true as boolean,
            isRequiredField: false as boolean,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            switch (this.$route.query.type) {
                case 'register':
                    this.isDisabled = false;
                    break;
                case 'edit':
                    this.requestTermAndConditionCategoryDetail();
                    break;
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const res: any = await this.$request.getRequest('ICM20800212', { categoryCode: this.categoryCode }, false)
                    this.termAndConditionCategoryDetail = {
                        categoryCode: res.body.categoryCode,
                        categoryName: res.body.categoryName,
                        categoryDesc: res.body.categoryDesc,
                        statusCode: res.body.statusCode,
                    }
                } else {
                    this.termAndConditionCategoryDetail = {
                        categoryCode: authDetailParsed.categoryCode,
                        categoryName: authDetailParsed.categoryName,
                        categoryDesc: authDetailParsed.categoryDesc,
                        statusCode: authDetailParsed.statusCode,
                    }
                }
            })
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        displayType() {
            return this.$route.query.type;
        },
        categoryCode() {
            return this.$route.query.categoryCode;
        },
        btnCancel(): boolean {
            if (this.$route.query.type == 'register' || this.isEdit == false) {
                return true;
            }
            return false
        },
        btnDelete(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit == true) {
                return true;
            }
            return false
        },
        btnList(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit == true) {
                return true;
            }
            return false;
        },
        btnEdit(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit == true) {
                return true
            }
            return false
        },
        btnSave(): boolean {
            if (this.$route.query.type == 'register' || this.isEdit == false) {
                return true;
            }
            return false;
        }
    },

    methods: {
        async requestTermAndConditionCategoryDetail() {
            await this.$request.getRequest('ICM20800212', { categoryCode: this.categoryCode }, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.termAndConditionCategoryDetail = res.body;
                        this.modifyCard = {
                            registerTellerId: res?.body.firstRegisterTellerID,
                            registerTellerName: res?.body.firstRegisterTellerName,
                            registerDate: res?.body.firstRegisterDate,
                            registerTime: res?.body.firstRegisterTime,
                            updatedTellerId: this.termAndConditionCategoryDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerID,
                            updatedTellerName: this.termAndConditionCategoryDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerName,
                            updatedDate: this.termAndConditionCategoryDetail.statusCode === '09' ? "" : res?.body.lastChangeDate,
                            updatedTime: this.termAndConditionCategoryDetail.statusCode === '09' ? "" : res?.body.lastChangeTime,
                            deleteTellerId: this.termAndConditionCategoryDetail.statusCode === '09' ? res?.body.lastChangeTellerID : "",
                            deleteTellerName: this.termAndConditionCategoryDetail.statusCode === '09' ? res?.body.lastChangeTellerName : "",
                            deleteDate: this.termAndConditionCategoryDetail.statusCode === '09' ? res?.body.lastChangeDate : "",
                            deleteTime: this.termAndConditionCategoryDetail.statusCode === '09' ? res?.body.lastChangeTime : "",
                        }
                    }
                });

        },

        async handleDelete() {
            await this.$request.getRequest('ICM20800241', { categoryCode: this.categoryCode }, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$router.push('ICM208002SC00')
                        this.$dialog.onOpen({
                            content: this.$t("message.delete_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        async handleRegister(body: ICM208002SC01_REQ_Body) {
            await this.$request.getRequest('ICM20800221', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$router.push('ICM208002SC00')
                        this.$dialog.onOpen({
                            content: this.$t("message.register_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        async handleEdit(body: ICM208002SC01_REQ_Body) {
            await this.$request.getRequest('ICM20800231', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.isEdit = true;
                        this.isDisabled = true;
                        this.$dialog.onOpen({
                            content: this.$t("message.update_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                this.requestTermAndConditionCategoryDetail()
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        handleSave() {
            const body: ICM208002SC01_REQ_Body = {
                categoryCode: this.termAndConditionCategoryDetail.categoryCode,
                categoryName: this.termAndConditionCategoryDetail.categoryName,
                categoryDesc: this.termAndConditionCategoryDetail.categoryDesc,
                statusCode: this.termAndConditionCategoryDetail.statusCode
            };
            switch (this.displayType) {
                case 'register':
                    this.handleRegister(body);
                    break;
                case 'edit':
                    this.handleEdit(body);
                    break;
            }
        },

        validateLength(event: any) {
            const inputValue = event.target.value;

            if (inputValue.length > 3) {
                return;
            }

            if (event.keyCode === 8 || event.key === 'Backspace' && event.keyCode !== 13 || !(/\d/.test(event.key))) {
                return event.preventDefault()
            }
        },

        handleClickActionButton(actionType: string) {
            switch (actionType) {
                case 'delete':
                    this.$dialog.onOpen({
                        content: this.$t("message.are_you_sure_you_want_to_delete"),
                        dialogMode: 'Question',
                        callback: () => {
                            modalController.dismiss();
                            this.handleDelete();
                        }
                    });
                    break;
                case 'cancel':
                    this.$dialog.onOpen({
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        dialogMode: 'Question',
                        callback: () => {
                            if (this.$route.query.type == 'register') {
                                this.$router.push('ICM208002SC00');
                            } else {
                                this.isDisabled = true;
                                this.isEdit = true;
                                this.requestTermAndConditionCategoryDetail()
                            }
                            modalController.dismiss();
                        }
                    })

                    break;
                case 'save':
                    if (!this.termAndConditionCategoryDetail.categoryName.trim()) {
                        this.isRequiredField = true;
                    } else {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: 'Question',
                            callback: () => {
                                modalController.dismiss();
                                this.handleSave();
                            }
                        })
                    }
                    break;
                case 'edit':
                    this.isDisabled = false;
                    this.isEdit = false;
                    break;
                default: // case list by default
                    this.$router.push('ICM208002SC00');
            }
        },
    },
})
